<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true"  :fit="false" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="服务器名称" prop="server_name">
                <el-input v-model="searchFormData.server_name" placeholder="请输入服务器名称"></el-input>
            </el-form-item>
            <el-form-item label="服务器编号" prop="server_no">
                <el-input v-model="searchFormData.server_no" placeholder="请输入服务器编号"></el-input>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)" >
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="server_name"
                    label="服务器名称"
                    width="160">
            </el-table-column>
            <el-table-column
                    prop="server_no"
                    label="服务器编号"
                    width="160">
            </el-table-column>
            <el-table-column
                    :formatter="format_server_state_code_id"
                    prop="server_state_code_id"
                    label="服务器状态"
                    width="90">
            </el-table-column>
            <el-table-column
                    prop="server_shelf_time"
                    label="上架时间"
                    width="90">
            </el-table-column>
            <el-table-column
                    prop="server_down_time"
                    label="下架时间"
                    width="90">
            </el-table-column>
            <el-table-column
                    prop="server_outer_ip"
                    label="外网ip"
                    width="140">
            </el-table-column>
            <el-table-column
                    prop="server_inner_ip"
                    label="内网ip"
                    width="140">
            </el-table-column>
            <el-table-column
                    prop="server_location"
                    label="服务器地址"
                    width="200"
                    :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                    prop="is_agent"
                    label="是否为代理"
                    width="90"
                    :formatter="format_is_agent">
            </el-table-column>
            <el-table-column
                    label="代理ID"
                    prop="agent_id"
                    width="240"
            >
            </el-table-column>
            <el-table-column
                    prop="remark"
                    label="备注">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="160">
                <template slot-scope="scope">
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="服务容器配置" placement="left">
                            <el-button @click.native.prevent="onServiceContainerConf(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-rongqi"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="查看" placement="left">
                            <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="修改" placement="left">
                            <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="6">
                        <el-tooltip class="item" effect="dark" content="删除" placement="left">
                            <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu"></i></el-button>
                        </el-tooltip>
                    </el-col>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'serverInfoManageList',
        data() {
            return {
                module_name: 'serverInfoManage',
                refresh_page_method: 'refresh_' + this.module_name,
                server_state_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 0,
                    server_name: '',
                    server_no: '',
                    order: '+server_no'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.server_state_code_list = await this.$setCodeList(this.server_state_code_list, 'server_state');
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            format_server_state_code_id(row, column, cellValue) {
                return this.$formatCodeValue(row, this.server_state_code_list, 'server_state_code_id')
            },
            format_is_agent(row, column, cellValue) {
                return row.is_agent === "true" ? '是' : '否';
            },
            async r_search_list(searchData) {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_server_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            async r_delete_Info(rowData) // 删除项目信息
            {
                let R = new this.$http();
                let {data} = await R.post(rowData, 'OAMP_delete_server_info');
                return data != null && data !== 'null';
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            search() // 查询按钮
            {
                let searchData = this.searchFormData;
                this.r_search_list(searchData)
            },
            onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.r_delete_Info(rowData)) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
            onServiceContainerConf(index, rows)  // 项目配置
            {
                this.$router.push({
                    name: 'serviceContainerConf',
                    params: {
                        serverId: rows[index]['_id']
                    }
                });
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



