<template>
    <el-dialog title="服务器信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')">
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
            <el-form-item label="服务器名称" prop="server_name">
                <el-input v-model="pageData.server_name"></el-input>
            </el-form-item>
            <el-form-item label="服务器编号" prop="server_no">
                <el-input v-model="pageData.server_no"></el-input>
            </el-form-item>
            <el-form-item label="上架时间" prop="server_shelf_time">
                <el-date-picker
                        v-model="pageData.server_shelf_time"
                        type="date"
                        placeholder="选择日期"
                        value-format="yyyy-MM-dd">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="下架时间" prop="server_down_time">
                <el-date-picker
                        v-model="pageData.server_down_time"
                        type="date"
                        placeholder="选择日期"
                        value-format="yyyy-MM-dd">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="外网ip" prop="server_outer_ip">
                <el-input v-model="pageData.server_outer_ip"></el-input>
            </el-form-item>
            <el-form-item label="内网ip" prop="server_inner_ip">
                <el-input v-model="pageData.server_inner_ip"></el-input>
            </el-form-item>
            <el-form-item label="服务器状态" prop="server_state_code_id">
                <el-select v-model="pageData.server_state_code_id" placeholder="请选择" clearable>
                    <el-option v-for="item in server_state_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="是否为代理" prop="server_state_code_id">
                <el-select v-model="pageData.is_agent" placeholder="请选择" clearable>
                    <el-option v-for="item in mongodb_bool_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="pageData.is_agent === 'true'" label="代理机房" prop="agent_motor_room">
                <el-input v-model="pageData.agent_motor_room"></el-input>
            </el-form-item>
            <el-form-item v-if="pageData.is_agent === 'true'" label="代理网络区域" prop="agent_network_area">
                <el-input v-model="pageData.agent_network_area"></el-input>
            </el-form-item>
            <el-form-item v-if="pageData.is_agent === 'true'" label="代理安全码" prop="agent_safety_code">
                <el-input v-model="pageData.agent_safety_code"></el-input>
            </el-form-item>
            <el-form-item label="服务器地址" prop="server_location">
                <el-input v-model="pageData.server_location" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel === 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel === 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'serverInfoManageInfo',
        data() {
            return {
                module_name: 'serverInfoManage',
                refresh_page_method: 'refresh_' + this.module_name,
                server_state_code_list: null,
                mongodb_bool_type_code_list: null,
                pageData: {
                    server_name: '',
                    server_no: '',
                    server_shelf_time: '',
                    server_down_time: '',
                    server_state_code_id: '',
                    server_outer_ip: '',
                    server_inner_ip: '',
                    serve_ips: [],
                    is_agent: 'false',
                    agent_motor_room: '',
                    agent_network_area: '',
                    agent_safety_code: '',
                    server_location: '',
                    remark: ''
                },
                pageDataRules: {
                    server_name: [{required: true, message: '请输入服务器名称', trigger: 'blur'}],
                    server_no: [{required: true, message: '请输入服务器编号', trigger: 'blur'}],
                    server_state_code_id: [{required: true, message: '请选择服务器状态', trigger: 'blur'}],
                }
            }
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name);
            }
        },
        async mounted() {
            this.server_state_code_list = await this.$setCodeList(this.server_state_code_list, 'server_state');
            this.mongodb_bool_type_code_list = await this.$setCodeList(this.mongodb_bool_type_code_list, 'mongodb_bool_type');
        },
        methods: {
            async r_insert_info(item) // 插入信息
            {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_insert_server_info');
                if (data != null && data !== 'null')
                    return data.data;
            },
            async r_update_info(item) // 修改信息
            {
                let R = new this.$http();
                // item['serve_ips'] = ['aa', 'bb', 'cc']; // 举例,实际该值由后端获取
                let {data} = await R.post(item, 'OAMP_update_server_info');
                if (data != null && data !== 'null')
                    return data;
            },
            onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.r_insert_info(item);
                        if (res != null) {
                            _this.$message.success('新增信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        }
                    }
                });
            },
            onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.r_update_info(item);
                        if (res != null) {
                            _this.$message.success('修改信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


